export default {
	name: 'local',
	data() {
		return {
			num: 0,
			citytab: [
				{
					name: '全部'
				},
				{
					name: '景点'
				},
				{
					name: '美食'
				},
				{
					name: '网红打卡'
				}
			]
		};
	},

	methods: {
		menubtn(index, name) {
			// console.log(index)
			// console.log(name)
			this.num = index;
			// 用子组件调用父组件方法
			this.$parent.fatherMethod(name);
		}
	}
};
